import React from "react";
import AttachMoneyIcon from "@mui/icons-material/AttachMoney";
import VerifiedIcon from "@mui/icons-material/Verified";
import PlayCircleIcon from "@mui/icons-material/PlayCircle";
import { CONFIDENCE_CARDS } from "../constants/data.ts";
import { yellow } from "@mui/material/colors";
import { Helmet } from "react-helmet";

const Confidence: React.FC<{ id: string }> = ({ id }) => {
  return (
    <section className="confidence" id={id}>
      <Helmet>
        
        <meta
          name="description"
          content="Shop with confidence at Ashia'a. We offer quality products, secure shopping, and reliable customer service. Enjoy a smooth online shopping experience with fast shipping and hassle-free returns."
        />
        <meta
          name="keywords"
          content="confidence, Ashia'a, secure shopping, quality assurance, online shopping, reliable customer service, fast shipping, hassle-free returns, trusted online store"
        />
      </Helmet>
      <div className="container">
        <div className="confidence-container">
          <div className="confidence-header-container">
            <h2 className="confidence-header">
              Shop with Confidence  Trusted Quality, Secure Shopping, and
              Satisfaction Guaranteed Every Time
            </h2>
          </div>
          <div className="confidence-cards">
            {CONFIDENCE_CARDS.map((card) => (
              <ConfidenceCard
                title={card.title}
                icon={card.icon}
                header={card.header}
                details={card.details}
                key={card.title}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

interface ConfidenceCardProps {
  title: string;
  header: string;
  details: string;
  icon: string;
}

const ConfidenceCard = ({
  title,
  header,
  details,
  icon,
}: ConfidenceCardProps) => {
  return (
    <div className="confidence-card">
      <p className="confidence-card-title">{title}</p>
      <div className="confidence-card-header">
        {icon === "money" ? (
          <AttachMoneyIcon
            className="confidence-card-icon"
            style={{ color: "yellow" }}
          />
        ) : (
          <VerifiedIcon
            className="confidence-card-icon"
            style={{ color: "#068dfe" }}
          />
        )}
        <h2 className="confidence-card-header">{header}</h2>
      </div>
      <p className="confidence-card-details">{details}</p>
      <div className="confidence-card-more-details">
        <div className="watch-video">
          <PlayCircleIcon /> <span>Watch Video</span>
        </div>
        <a className="confidence-card-learn-more">Learn More</a>
      </div>
    </div>
  );
};

export default Confidence;
