import React, { useState } from "react";
import { FOOTER_LINKS } from "../constants/data.ts";
import { Link } from "react-router-dom";
import FacebookIcon from "@mui/icons-material/Facebook";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import XIcon from "@mui/icons-material/X";
import emailjs from "emailjs-com";
import Swal from "sweetalert2";
import { Helmet } from "react-helmet";

const Footer = () => {
  const [message, setMessage] = useState("");

  const handleSendMessage = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.preventDefault();

    const templateParams = {
      message: message,
    };

    emailjs
      .send(
        "service_3kddl17",
        "template_nswl1nh",
        templateParams,
        "D3UURIdEGkdNZuikp"
      )
      .then(() => {
        Swal.fire({
          icon: "success",
          title: "Message Sent",
          text: "Your message has been successfully sent to the owner this website",
          confirmButtonColor: "#3085d6",
        });
        setMessage("");
      })
      .catch((err) => {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong! Please try again.",
          confirmButtonColor: "#d33",
        });
      });
  };

  return (
    <footer className="footer">
      <Helmet>
        
        <meta
          name="description"
          content="Need assistance? Reach out to Ashia'a for any inquiries, support, or feedback. Our customer service team is here to help you with any questions you may have about your shopping experience."
        />
        <meta
          name="keywords"
          content="contact us, Ashia'a, customer support, inquiries, feedback, online store support, contact form, help center"
        />
      </Helmet>
      <div className="container links">
        {FOOTER_LINKS.map((column) => (
          <ColumnLinks
            title={column.title}
            links={column.links}
            key={column.title}
          />
        ))}
        <div className="payment-follow">
          <div className="payment">
            <h3 style={{ color: "white", fontWeight: "700", fontSize: "24px" }}>
              Payment Methods
            </h3>
            <p style={{ color: "white", fontSize: "19px" }}>
              Visa | MasterCard | PayPal{" "}
            </p>
          </div>
          <div className="follow">
            <h3 style={{ color: "white", fontWeight: "700", fontSize: "24px" }}>
              Follow
            </h3>
            <div className="follow-icons">
              <Link to="/">
                <FacebookIcon className="facebook" />
              </Link>
              <Link to="https://www.linkedin.com/in/jebreal-aldrani-623770260/">
                <LinkedInIcon className="linked-in" />
              </Link>
              <Link to="/">
                <XIcon className="x" />
              </Link>
              <Link to="https://github.com/JebrealAldrani">
                <GitHubIcon className="github" />
              </Link>
            </div>
          </div>
        </div>
        <div className="contact">
          <textarea
            placeholder="Enter Your Message Here..."
            className="text-area"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
          />
          <button
            type="submit"
            className="contact-email-btn"
            onClick={handleSendMessage}
          >
            Send Message
          </button>
        </div>
      </div>
      <div
        style={{
          backgroundColor: "#eee",
          width: "100%",
          height: "1px",
          marginTop: "20px",
        }}
      ></div>
      <div
        className="container"
        style={{ textAlign: "center", color: "white" }}
      >
        <p>
           2025{" "}
          <Link
            to="https://www.linkedin.com/in/jebreal-aldrani-623770260/"
            style={{ color: "white" }}
          >
            <strong>Jebreal Aldrani</strong>
          </Link>
          . All rights reserved.
        </p>
      </div>
    </footer>
  );
};

interface ColumnLinksProps {
  title: string;
  links: { name: string; url: string }[];
}

const ColumnLinks = ({ title, links }: ColumnLinksProps) => {
  return (
    <div className="column-links">
      <h3 style={{ fontSize: "24px", color: "white", fontWeight: "700" }}>
        {title}
      </h3>
      {links.map((link) => (
        <Link
          to={link.url}
          key={link.name}
          style={{ color: "white", fontSize: "19px" }}
        >
          {link.name}
        </Link>
      ))}
    </div>
  );
};

export default Footer;
