import React, { useId } from "react";
import ImageGallery from "react-image-gallery";
import StarIcon from "@mui/icons-material/Star";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import { ITEM } from "../constants/data.ts";
import { useLocation } from "react-router";
import Swal from "sweetalert2";

const Item = () => {
  const location = useLocation();
  const id = location.pathname.split("/")[2];
  const images = ITEM[id];

  const addToCart = () => {
    if(localStorage.getItem(id) === null) {
      localStorage.setItem(id, images[0].original);
    Swal.fire({
      title: "The Item Added To Cart Successfully",
      icon: "success",
      text: "Go To Cart To Complete The Purchase Process"
    })
    } else {
      Swal.fire({
        title: "The Item Is Already In The Cart",
        icon: "info",
        text: "Go To Cart To Complete The Purchase Process"
      })
    }
    
  }

  return (
    <div className="item">
      <div className="container">
        <div className="image-gallery">
          <ImageGallery
            items={images}
            showPlayButton={false}
            showFullscreenButton={false}
            renderItem={(item) => (
              <div
                style={{ width: "100%", height: "300px", overflow: "hidden" }}
              >
                <img
                  src={item.original}
                  alt=""
                  style={{
                    width: "100%",
                    height: "100%",
                    objectFit: "cover",
                    borderRadius: "10px",
                  }}
                />
              </div>
            )}
          />
        </div>
        <div className="description">
          <div className="item-desc">
            <p>
              Discover elegance and quality with this premium item, designed to
              meet your needs with style and durability. Crafted from
              high-quality materials, it offers a perfect blend of functionality
              and sophistication. Whether for personal use or as a thoughtful
              gift, this item is a must-have addition to your collection. Don't
              miss out on this exceptional value!
            </p>
            <div className="item-rating">
              <div className="rating">
                <StarIcon style={{ color: "gold" }} />
                <StarIcon style={{ color: "gold" }} />
                <StarIcon style={{ color: "gold" }} />
                <StarIcon style={{ color: "gold" }} />
                <StarBorderIcon />
              </div>
              <span>176 rating | 1974 sales</span>
            </div>
            <hr />
          </div>
          <div className="item-price">
            <div className="item-price">35 $</div>
            <button type="button" className="addCartBtn" onClick={addToCart}>
              Add to cart
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Item;
