import React from "react";
import { Helmet } from "react-helmet";

function LatestSale({ id, img, price, sales }) {
  return (
    <>
      <section
        className="latestSale"
        style={{
          background: `url(${img})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          width: "100%",
          height: "300px",
          borderRadius: "6px",
        }}
      >
        <Helmet>
          
          <meta
            name="description"
            content="Explore Ashia'a's latest sales and grab the best deals on top-quality products! From electronics to fashion, find amazing discounts and limited-time offers on our hottest products."
          />
          <meta
            name="keywords"
            content="latest sales, Ashia'a, sales, best deals, online store, electronics sale, fashion sale, limited-time offers, affordable products, top products"
          />
        </Helmet>
        <div className="latestSalePrice">{price} $</div>
        <div className="sales"> + {sales}K</div>
      </section>
    </>
  );
}

export default LatestSale;
