import React from "react";
import Product from "./components/Product.tsx";
import { PRODUCTS } from "./constants/data.ts";
import { Helmet } from "react-helmet";

// Define the structure of a single product
interface ProductType {
  id: string;
  title: string;
  desc: string;
  rate: number;
  price: number;
  img: string;
}

const Products: React.FC<{ id: string }> = ({ id }) => {
  // Type productsShow as an array of JSX.Element
  const productsShow: JSX.Element[] = PRODUCTS.map((product) => (
    <Product
      key={product.id}
      id={product.id}
      title={product.title}
      desc={product.desc}
      rate={product.rate}
      price={product.price}
      img={product.img}
    />
  ));
  console.log(productsShow);
  return (
    <section className="products" id={id}>
      <Helmet>
        
        <meta
          name="description"
          content="Browse through Ashia'a's extensive product selection, from fashion to electronics, home goods, and more. Enjoy great deals, fast shipping, and top-quality products for all your needs."
        />
        <meta
          name="keywords"
          content="products, Ashia'a, online store, e-commerce, quality products, fashion, electronics, home goods, affordable products, buy products online, best deals"
        />
      </Helmet>
      <h2 className="main-title">Products</h2>
      <div className="container">{productsShow}</div>
    </section>
  );
};

export default Products;
