import React from "react";
import { useRef } from "react";
import ImageGallery from "react-image-gallery";
import { DISCOUNTS } from "../constants/data.ts";
import { Helmet } from "react-helmet";

const Discounts: React.FC<{ id: string }> = ({ id }) => {
  const galleryRef = useRef<ImageGallery | null>(null);

  const handleMouseEnter = () => {
    if (galleryRef.current) {
      galleryRef.current.pause();
    }
  };

  const handleMouseLeave = () => {
    if (galleryRef.current) {
      galleryRef.current.play();
    }
  };
  return (
    <>
      <section className="discounts" id={id}>
        <Helmet>
          
          <meta
            name="description"
            content="Discover amazing discounts and exclusive offers on Ashia'a! Shop a variety of quality products at unbeatable prices, including fashion, electronics, and more. Limited time deals await!"
          />
          <meta
            name="keywords"
            content="discounts, Ashia'a, sales, exclusive offers, online store, fashion discounts, electronics sales, best deals, shop discounts, affordable products"
          />
        </Helmet>
        <h2 className="main-title">Discounts</h2>
        <div className="col-12">
          <ImageGallery
            ref={galleryRef}
            items={DISCOUNTS}
            showThumbnails={false}
            showFullscreenButton={true}
            showPlayButton={false}
            showBullets={true}
            slideDuration={600}
            autoPlay={true}
            style={{ width: "100%" }} // Ensures full width
            renderItem={(item) => (
              <img
                src={item.original}
                alt={item.originalAlt}
                width="100%"
                height="auto"
                style={{
                  width: "100%",
                  maxWidth: "100%",
                  height: "auto",
                  objectFit: "contain",
                }}
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
              />
            )}
          />
        </div>
      </section>
    </>
  );
};

export default Discounts;
