import React from "react";
import StarIcon from "@mui/icons-material/Star";
import StarBorderIcon from "@mui/icons-material/StarBorder";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import { Link } from "react-router";
import Swal from "sweetalert2";

interface productPropsType {
  id: string;
  title: string;
  desc: string;
  rate: number;
  price: number;
  img: string;
}

function Product({ id, title, desc, rate, price, img }: productPropsType) {
  const addToCart = () => {
      if(localStorage.getItem(id) === null) {
        localStorage.setItem(id, img);
      Swal.fire({
        title: "The Item Added To Cart Successfully",
        icon: "success",
        text: "Go To Cart To Complete The Purchase Process"
      })
      } else {
        Swal.fire({
          title: "The Item Is Already In The Cart",
          icon: "info",
          text: "Go To Cart To Complete The Purchase Process"
        })
      }
      
    }
  return (
    <div className="product">
      <Link to={`/Item/${id}`}>
      <div
        className="productImg"
        style={{
          background: `url(${img})`,
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          backgroundSize: "cover",
          width: "100%",
          height: "200px",
        }}
      ></div></Link>
      

      <div className="productInfo">
        <h3>{title}</h3>
        <p>{desc}</p>
      </div>
      <div className="productFooter">
        <div className="priceAndRating">
          <div className="rating">
            {[...Array(5)].map((_, i) =>
              i < rate ? (
                <StarIcon key={i} style={{ color: "gold" }} />
              ) : (
                <StarBorderIcon key={i} />
              )
            )}
          </div>
          <div className="price">{price}$</div>
        </div>
        <div className="addCart" onClick={addToCart}>
          <ShoppingCartOutlinedIcon />
        </div>
      </div>
    </div>
  );
}

export default Product;
