import React, { useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import RemoveIcon from "@mui/icons-material/Remove";
import AddIcon from "@mui/icons-material/Add";

const Cart = () => {
  const item1 = localStorage.getItem("p3");
  const item2 = localStorage.getItem("p4");
  return (
    <div className="cart">
      <div className="container">
        <div className="cart-items-container">
          <CartItem image={item1} />
          <CartItem image={item2} />
        </div>
        <div className="buy-process-container">
          <div className="order-summary">
            <div className="summary-item">
              <span>Total Request:</span>
              <span>$70.00</span>
            </div>
            <div className="summary-item">
              <span>Shipping Cost:</span>
              <span>$10.00</span>
            </div>
            <div className="summary-item total">
              <span>Total:</span>
              <span>$80.00</span>
            </div>
            <button className="complete-order-btn">Complete Order</button>
          </div>
        </div>
      </div>
    </div>
  );
};

const CartItem = ({ image }) => {
  const [number, setNumber] = useState(1);

  const add = () => {
    setNumber((prev) => ++prev);
  };

  const minus = () => {
    number !== 0 && setNumber((prev) => --prev);
  };

  return (
    <div className="cart-item">
      <div className="cart-item-container">
        <div
          className="cart-item-image"
          style={{ backgroundImage: `url(${image})` }}
        ></div>
        <div className="cart-item-content">
          <div className="cart-item-desc">
            <p>
              Discover elegance and quality with this premium item, designed to
              meet your needs with style and durability. Crafted from
              high-quality materials, 
            </p>
            <DeleteIcon className="delete-icon" />
          </div>
          <div className="cart-item-price">
            <p>Price: 35$</p>
            <div className="cart-item-number">
              <span className="minus" onClick={minus}>
                <RemoveIcon />
              </span>
              {number}
              <span className="plus" onClick={add}>
                <AddIcon />
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cart;
