import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./style.min.css";
import Header from "./components/Header.tsx";
import Landing from "./components/Landing.tsx";
import Products from "./Products.tsx";
import Discounts from "./components/Discounts.tsx";
import LatestSales from "./LatestSales.tsx";
import AboutUs from "./components/AboutUs.tsx";
import Confidence from "./components/Confidence.tsx";
import Categories from "./components/Categories.tsx";
import Steps from "./components/Steps.tsx";
import Footer from "./components/Footer.tsx";
import Item from "./components/Item.tsx";
import { Routes, Route } from "react-router";
import Layout from "./components/Layout.tsx";
import ScrollToTop from "./components/ScrollTop.tsx";
import Cart from "./components/Cart.tsx";

export default function App() {
  return (
    <>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <Layout>
                <Landing id="home" />
                <AboutUs id="about-us" />
                <Categories id="categories" />
                <Products id="products" />
                <Discounts id="discounts" />
                <LatestSales id="latest-sales" />
                <Confidence id="guaranties" />
                <Steps id="steps" />
              </Layout>
            </>
          }
        ></Route>
        <Route
          path="/Item/:id"
          element={
            <>
              <Layout>
                <Item />
              </Layout>
            </>
          }
        />
        <Route
          path="/cart"
          element={
            <>
              <Layout>
                <Cart />
              </Layout>
            </>
          }
        />
      </Routes>
    </>
  );
}
