import React from "react";
import { STATISTICS } from "../constants/data.ts";
import { Helmet } from "react-helmet";

const AboutUs: React.FC<{id: string}> = ({id}) => {
  return (
    <section className="aboutUs" id={id}>
      <Helmet>
  <meta
    name="description"
    content="Learn more about Ashia'a, a trusted online store committed to providing the best quality products in fashion, electronics, and home goods. Our mission is to offer top-notch products with exceptional customer service."
  />
  <meta
    name="keywords"
    content="about us, Ashia'a, e-commerce, online store, quality products, fashion, electronics, home goods, customer service, mission"
  />
      </Helmet>
      <h2 className="main-title">About Us</h2>
      <div className="container about-us-container">
        <div className="about-us-content">
          <div className="about-paragraph">
            Welcome to <strong>Ashia'a</strong>, your trusted destination for
            high-quality products at unbeatable prices. We are passionate about
            delivering a seamless shopping experience, combining convenience,
            affordability, and exceptional customer service.
            <br /> At <strong>Ashia'a</strong>, we handpick every item to ensure
            top quality, offering a wide range of products to meet your needs.
            Whether youre looking for the latest trends, everyday essentials,
            or unique finds, weve got you covered. <br />
            Our mission is to make online shopping easy and enjoyable, with fast
            shipping, secure payments, and a commitment to customer
            satisfaction. Thank you for choosing <strong>Ashia'a</strong>where
            shopping meets excellence!
          </div>
          <div className="about-image"></div>
        </div>
        <div className="about-statistics">
            {STATISTICS.map((card) => (
                <AboutStatistic number={card.number} title={card.title} key={card.title}/>
            ))}
        </div>
      </div>
    </section>
  );
};

interface AboutStatisticProps {
    number: string,
    title: string
}

const AboutStatistic = ({ number, title }: AboutStatisticProps) => {
    return (
        <div className="statistic-card">
            <h3 className="statistic-number">{number}</h3>
            <p className="statistic-title">{title}</p>
        </div>
    )
}

export default AboutUs;
