import React from "react";

import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Container from "@mui/material/Container";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import Tooltip from "@mui/material/Tooltip";
import MenuItem from "@mui/material/MenuItem";
import AdbIcon from "@mui/icons-material/Adb";
import { Height } from "@mui/icons-material";
import { Link } from "react-scroll";
import {Link as RouterLink} from "react-router-dom"

const pages = [
  "Home",
  "About",
  "Categories",
  "Products",
  "Discounts",
  "Latest",
];
const settings = ["Profile", "Account", "Dashboard", "Logout"];

export default function Header() {
  const [anchorElNav, setAnchorElNav] = React.useState<null | HTMLElement>(
    null
  );
  const [anchorElUser, setAnchorElUser] = React.useState<null | HTMLElement>(
    null
  );

  const handleOpenNavMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <AppBar
      position="static"
      sx={{
        borderRadius: "5px",
        borderBottom: "2px solid white",
        backgroundColor: "#034F2B",
      }}
    >
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Typography
            variant="h6"
            noWrap
            component={RouterLink}
            to="/"
            sx={{
              mr: 2,
              display: { xs: "none", md: "flex" },
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            <img
              src="/assets/images/logo3.webp"
              alt="Logo"
              style={{ height: "68px" }}
            />
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{ display: { xs: "block", md: "none" } }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Link
                    to={page.toLowerCase().replace(/\s+/g, "-")} // Ensure section IDs match
                    smooth={true}
                    onClick={handleCloseNavMenu}
                    duration={500}
                    spy={true}
                    offset={-70} // Adjust if you have a fixed header
                    style={{
                      textDecoration: "none",
                      color: "inherit",
                      width: "100%",
                      cursor: "pointer",
                    }}
                    aria-label={`Go to ${page} section`} // Add descriptive label for screen readers
                  >
                    <Typography
                      sx={{
                        textAlign: "start",
                        width: "300px",
                        borderBottom: "1px solid #eee",
                      }}
                    >
                      {page}
                    </Typography>
                  </Link>
                </MenuItem>
              ))}
              <MenuItem  onClick={handleCloseNavMenu}>
                  <RouterLink
                    to="cart" // Ensure section IDs match
                    onClick={handleCloseNavMenu}
                    aria-label={`Go to cart page`} // Add descriptive label for screen readers
                    style={{
                      textDecoration: "none",
                      color: "inherit",
                      width: "100%",
                      cursor: "pointer",
                      
                    }}
                  >
                    <Typography
                      sx={{
                        textAlign: "start",
                        width: "300px",
                        borderBottom: "1px solid #eee",
                      }}
                    >
                      Cart
                    </Typography>
                  </RouterLink>
                </MenuItem>
            </Menu>
          </Box>
          {/* <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} /> */}
          <Typography
            variant="h5"
            noWrap
            component={RouterLink}
            to="/"
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontFamily: "monospace",
              fontWeight: 700,
              letterSpacing: ".3rem",
              color: "inherit",
              textDecoration: "none",
            }}
          >
            <img
              src="assets/images/logo3.webp"
              alt="Logo"
              style={{ height: "68px" }}
            />
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
            {pages.map((page) => (
              <Link
                key={page}
                to={page.toLowerCase().replace(/\s+/g, "-")} // Ensure section IDs match
                smooth={true}
                duration={500}
                spy={true}
                offset={-70} // Adjust if you have a fixed header
                aria-label={`Go to ${page} section`} // Add descriptive label for screen readers
                style={{
                  textDecoration: "none",
                  color: "inherit",
                  
                  cursor: "pointer",
                }}
              >
                <Button
                  key={page}
                  onClick={handleCloseNavMenu}
                  sx={{
                    my: 2,
                    color: "white",
                    display: "block",
                    fontWeight: "600",
                    borderBottom: "2px solid transparent",
                    "&:hover": {
                      borderBottom: "2px solid white",
                    },
                  }}
                >
                  {page}
                </Button>
              </Link>
            ))}
            <RouterLink
                to="cart" // Ensure section IDs match
                aria-label={`Go to cart page`} // Add descriptive label for screen readers
                style={{
                  textDecoration: "none",
                  color: "inherit",
                  
                  cursor: "pointer",
                }}
              >
                <Button
                  
                  onClick={handleCloseNavMenu}
                  sx={{
                    my: 2,
                    color: "white",
                    display: "block",
                    fontWeight: "600",
                    borderBottom: "2px solid transparent",
                    "&:hover": {
                      borderBottom: "2px solid white",
                    },
                  }}
                >
                  Cart
                </Button>
              </RouterLink>
          </Box>
          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Open settings">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <Avatar
                  src="/assets/images/profile.webp"
                  alt="Jebreal Aldrani"
                />
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map((setting) => (
                <MenuItem key={setting} onClick={handleCloseUserMenu}>
                  <Typography sx={{ textAlign: "center" }}>
                    {setting}
                  </Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
