import React, { useRef } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import { LATEST_SALES } from "./constants/data.ts";
import { Swiper as SwiperType } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import LatestSale from "./components/LatestSale.tsx";

// Define the structure of a single product
interface ProductType {
  id: string;
  title: string;
  desc: string;
  rate: number;
  price: number;
  img: string;
}

// Define the props for the ProductList component
interface ProductListProps {
  products: ProductType[];
}

const LatestSales: React.FC<{id: string}> = ({id}) => {
  // Use a ref to store the swiper instance
  const swiperRef = useRef<SwiperType | null>(null);

  // Type productsShow as an array of JSX.Element
  const productsShow: JSX.Element[] = LATEST_SALES.map((product) => (
    <SwiperSlide
      key={product.id}
      className="swiper"
      onMouseEnter={() => swiperRef.current?.autoplay.stop()}
      onMouseLeave={() => swiperRef.current?.autoplay.start()}
    >
      <LatestSale
        id={product.id}
        img={product.img}
        price={product.price}
        sales={product.sales}
      />
    </SwiperSlide>
  ));

  return (
    <>
      <section className="latestSales" id={id}>
        <h2 className="main-title">Latest Sales</h2>
        <div style={{ padding: "0 20px" }}>
          {" "}
          {/* Wrapper div with 20px horizontal padding */}
          <Swiper
            modules={[Autoplay]}
            spaceBetween={50}
            loop={true}
            speed={800}
            autoplay={{
              delay: 3000,
              disableOnInteraction: false,
            }}
            onSlideChange={() => console.log("slide change")}
            onSwiper={(swiper) => {
              swiperRef.current = swiper; // Store the swiper instance in the ref
            }}
            breakpoints={{
              640: {
                slidesPerView: 1,
              },
              768: {
                slidesPerView: 2,
              },
              1024: {
                slidesPerView: 4,
              },
            }}
          >
            {productsShow}
          </Swiper>
        </div>
      </section>
    </>
  );
}

export default LatestSales;
