import React from "react";
import { Helmet } from "react-helmet";

const Landing: React.FC<{ id: string }> = ({ id }) => {
  return (
    <section
      id={id}
      style={{
        height: window.innerWidth < 768 ? "70vh" : "100vh",
      }}
      className="landing"
    >
      <Helmet>
        <meta
          name="description"
          content="Shop the best quality products at Ashia'a. From fashion to electronics, find everything you need with fast shipping and great deals. Explore our latest collections now!"
        />
        <meta
          name="keywords"
          content="online store, e-commerce, Ashia'a, buy products online, fashion, electronics, home goods, affordable products, best deals, quality products"
        />
      </Helmet>
      <h2>Anything you need is available here</h2>
      <h3>Place an order, and the item will be delivered to your home</h3>
    </section>
  );
};

export default Landing;
