import React from "react";
import { CATEGORIES } from "../constants/data.ts";
import { Helmet } from "react-helmet";

const Categories: React.FC<{ id: string }> = ({ id }) => {
  return (
    <section className="categories" id={id}>
      <Helmet>
        
        <meta
          name="description"
          content="Explore Ashia'a's wide range of categories, from fashion to electronics, home goods, and more. Find the best deals and top-quality products across all categories for every need."
        />
        <meta
          name="keywords"
          content="categories, Ashia'a, online store, e-commerce, fashion, electronics, home goods, quality products, buy products online, best deals"
        />
      </Helmet>
      <div className="container">
        <h2 className="main-title">Categories</h2>
        <div className="categories-container">
          {CATEGORIES.map((category) => (
            <CategoryCard
              image={category.image}
              title={category.title}
              key={category.title}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

interface CategoryCardProps {
  image: string;
  title: string;
}

const CategoryCard = ({ image, title }: CategoryCardProps) => {
  return (
    <div className="category-card-container">
      <div
        className="category-img"
        style={{ backgroundImage: `url(${image})` }}
      ></div>
      <p className="category-name">{title}</p>
    </div>
  );
};

export default Categories;
